import { Routes, RouterModule } from '@angular/router';
import { ModuleWithProviders } from '@angular/core';
//
import { ChannelDeliveryComponent } from './channel-delivery/channel-delivery.component';
import { ChannelDeliveryDetailsComponent } from './channel-delivery/channel-delivery-details/channel-delivery-details.component';//--
import { ProductLowerFrameComponent } from './channel-delivery/product-lower-frame/product-lower-frame.component';//
import { DeliveryQueryComponent } from './delivery-query/delivery-query.component';//
import { SiteQueryDetailsComponent } from './delivery-query/site-query-details/site-query-details.component';
import { BatchQueryDetailsComponent } from './delivery-query/batch-query-details/batch-query-details.component';
import { ProQueryDetailsComponent } from './delivery-query/pro-query-details/pro-query-details.component';
const routes: Routes = [
    {
        path: '',
        children: [
            {
                path: 'channel-delivery', component: ChannelDeliveryComponent,
            },
            {
                path: 'channel-delivery-details', component: ChannelDeliveryDetailsComponent,
            },
            {
                path: 'product-lower-frame', component: ProductLowerFrameComponent,
            },
            {
                path: 'delivery-query', component: DeliveryQueryComponent,
            },
            {
                path: 'site-query-details', component: SiteQueryDetailsComponent,
            },
            {
                path: 'batch-query-details', component: BatchQueryDetailsComponent,
            },
            {
                path: 'pro-query-details', component: ProQueryDetailsComponent,
            },
        ]
    },

];

export const ProductLaunchRouting: ModuleWithProviders = RouterModule.forChild(routes);
