import { Component, OnInit } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { ProductManageHttpService } from "./../../../../http/product-manage.http.service";
import { SelectItem, Message } from 'primeng/primeng';
import { Commfunc } from '../../../../../../../@uisftech/common/service/commonFun';

@Component({
  selector: 'app-pro-query-details',
  templateUrl: './pro-query-details.component.html',
  styleUrls: ['./pro-query-details.component.scss'],
  providers: [ProductManageHttpService, Commfunc]
})
export class ProQueryDetailsComponent implements OnInit {
  batchNum: string; // 
  msgs: Message[] = []; // 
  showClass: string; // //ID
  showClassSite: string; // 
  productList: any = []; // ID
  productListN: any = []; // 
  siteList: any = []; // 
  functionList: any = []; // 
  labelList: any = []; // 
  productDeliveryType: any; // 
  code: any;
  constructor(public productManageHttpService: ProductManageHttpService,
    public router: ActivatedRoute,
    public commfunc: Commfunc) {
    this.codeValues(); // 
    this.productDeliveryType = this.code['ProductDeliveryType'];
  }

  ngOnInit() {
    // this.productDeliveryType = [
    //   { value: '23', label: '' },
    //   { value: '24', label: '' },
    //   { value: '25', label: '' },
    //   { value: '26', label: '' },
    //   { value: '27', label: '' },
    //   { value: '28', label: '' },
    //   { value: '32', label: '' },
    //   { value: '33', label: '' },
    //   { value: '99', label: '' },
    //   { value: '98', label: '' },
    //   { value: 'ON', label: '' },
    //   { value: 'OFF', label: '' },
    // ];
    this.router.params.subscribe((data) => {
      this.productList = JSON.parse(data['productId']);
    });
    this.productListN = this.productList;
  }
  // 
  codeValues() {
    this.commfunc.codeValue();
    let ret = this.commfunc.codeDatas;
    return this.code = ret; // code
  }
  // 
  productClick(col) {
    this.siteList = [];
    this.functionList = [];
    this.labelList = [];
    this.batchNum = '';
    this.showClass = col.productId;
    let parm = {
      productId: col.productId,
      channelId: '',
    };
    this.productManageHttpService.queryByProductId(parm).subscribe(data => {
      if (data.returnCode.code == "success") {
        this.siteList = data.launchInfo.channelList;
      } else {
        this.msgs = [];
        this.msgs.push({ severity: 'error', summary: '', detail: data.errorMessage });
      }
    }, error => {
      this.msgs = [];
      this.msgs.push({ severity: 'error', summary: '', detail: '' });
    });
  }
  // 
  siteClick(col) {
    this.functionList = [];
    this.labelList = [];
    this.showClassSite = col.channelId;
    let parm = {
      channelId: col.channelId,
      productId: this.showClass,
    };
    this.productManageHttpService.queryByProductId(parm).subscribe(data => {
      if (data.returnCode.code == "success") {
        this.functionList = data.launchInfo.functionList;
        this.labelList = data.launchInfo.lableList;
        this.batchNum = data.launchInfo.applySerialNo;
      } else {
        this.msgs = [];
        this.msgs.push({ severity: 'error', summary: '', detail: data.errorMessage });
      }
    }, error => {
      this.msgs = [];
      this.msgs.push({ severity: 'error', summary: '', detail: '' });
    });
  }
  goBack() {
    window.history.go(-1);
  }
}
